
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  /* color: #2c3e50; */
  /* margin-top: 60px; */
}
body {
  margin: 0;
  padding: 0;
}
p {
  margin: 0;
  padding: 0;
}

.date {
  width: 100vw;
  height: 600px;
}

.dateV-wrapper {
  width: 100%;
  height: 100%;
  background: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.dateV-calend-wrapper {
  width: 100%;
  /* height: 100%; */
  /* flex: 1 1 auto; */
}
.date-calend-header {
  height: 100px;
  width: 100%;
  z-index: 100;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background: red;
}
.last-btn,
.next-btn {
  width: 100px;
  height: 100px;
  text-align: center;
  line-height: 100px;
}
.jump-btn {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.jump-btn > input {
  width: 50px;
}
.date-calend-body-wrapper {
  overflow: auto;
  margin-top: 100px;
}
.calend-month {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.calend-day {
  width: calc(100vw / 7);
  height: calc(100vw / 7);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.calend-day div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #978e8e;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
}
.before-month,
.later-month {
  /* background: #e6e6e6; */
  color: #d9d9d9;
}
.cu-month {
  /* background: #ec57c8; */
  color: #000000;
}
.active {
  background: #00daf9;
}
.today {
  color: red;
}

.dataV-details-wrapper {
  -webkit-box-flex: 2;
      -ms-flex: 2 2 auto;
          flex: 2 2 auto;
  background: wheat;
}
